<script>
import { Line } from 'vue-chartjs'

export default {
    extends: Line,
    props: {
        chartdata: {
            type: Object,
            default: null
        }, 
        options: {
            type: Object,
            default: null
        },
    },
    data() {
        return {
            gradient: null,
        }
    },
    mounted() {
        this.renderChart(this.chartdata, this.options);
    },
    watch: {
        chartdata() {
            this.renderChart(this.chartdata, this.options);
        }
    }
}
</script>